import React, { useState } from 'react'
import Button from './Button'
import { VOTE_NEW_ROUND, GET_SPLITTED_LIST, GET_VOTE_STATE } from '../queries/queries'
import { useMutation } from '@apollo/client'
import loadingAnimation from '../resources/loading.gif'

const LastVoteLandingPage = ({ setBody, leftList, rightList, voteState }) => {

  const [nextRoundVoted, setNextRoundVoted] = useState(false)

  const [launchVoteNewRound] = useMutation(VOTE_NEW_ROUND, {
    refetchQueries: [{ query: GET_SPLITTED_LIST }, { query: GET_VOTE_STATE }]
  })

  const handleNextRound = () => {
    launchVoteNewRound()
    setNextRoundVoted(true)
    setBody('topicSelection')
  }

  //rendaa loading animation + votet uudelle kierrokselle
  if (nextRoundVoted) {
    return (
      <div className='Container'>
        <img className='Loading-animation' src={loadingAnimation} alt='loading animation' />
        {/* pseudoa */}
        <p>{voteState.newRoundVotes}/{voteState.voterCount} valmiina seuraavalle kierrokselle.</p>
      </div>
    )
  } else {
    return (
      <div className='Container'>
        <h3>Äänet annettu.</h3>

        {/* leftList wins */}
        {leftList.votes > rightList.votes && <div className='Container-flex'>
          <div className='Container-left Victor'>
            {leftList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
          </div>
          <div className='Container-right'>
            {rightList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
          </div>
        </div>}

        {/* rightList wins */}
        {leftList.votes < rightList.votes && <div className='Container-flex'>
          <div className='Container-left'>
            {leftList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
          </div>
          <div className='Container-right Victor'>
            {rightList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
          </div>
        </div>}
        <Button type='TopicSelection-Choice-button' text='Seuraavalle kierrokselle' handleClick={() => handleNextRound()} />
      </div>
    )
  }
}

export default LastVoteLandingPage
